.header {
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(3,37,65, 1);
	min-height: 64px;
	width: 100%;
	transition: top 0.2s linear;
}

.header__logo {
	.logo {
		display: block;
		width: 154px;
	}
}

.header__wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.header__right {
	display: flex;
	align-items: center;
	justify-content: center;

	.user-inbox {
		margin-right: 32px;
	}

	.btn.btn--text {
		border-bottom-color: #fff;
		color: #fff;
	}
}

.header__title {
	color: #fff;
}

.header__user {
	margin-right: 32px;
	color: #fff;
	font-size: 16px;

	a {
		font-size: inherit;
		color: inherit;
	}
}
