.btn {
	position: relative;
	display: inline-flex;
	vertical-align: middle;
	text-align: center;
	box-sizing: border-box;
	align-items: center;
	justify-content: center;
	text-decoration: none !important;
	outline: none;
	-webkit-appearance: none;
	border: none;
	padding: 10px 16px;
	border-radius: 4px;
	cursor: pointer;
	user-select: none;
	background-color: transparent;
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
	font-size: 16px;
	text-transform: uppercase;
	color: #fff;

	&.btn--primary {
		background-image: linear-gradient(to right, rgba(30,213,169, 1) 0%, rgba(1,180,228, 1) 100%);

		&:hover {
			background-image: linear-gradient(to right, rgba(1,180,228, 1) 0%, rgba(30,213,169, 1) 100%);
		}
	}

	&.btn--text {
		background-image: none;
		padding: 0;
		border-radius: 0;
		border-bottom: 1px solid rgba(3,37,65, 1);
		line-height: 1;
		text-transform: capitalize;
		color: rgba(3,37,65, 1);
	}

	&.btn--secondary {
		background-image: none;
		border: 1px solid rgba(30,213,169, 1);
		color: rgba(3,37,65, 1);

		&:hover {
			box-shadow: -5px -5px 0 0 rgba(30,213,169, 1);
		}
	}

	&.disabled,
	&:disabled{
		box-shadow: -5px -5px 0 0 #c7cccf;
		color: #949c9e !important;
		border: none;
		text-shadow: none;
		cursor: default !important;
		background: #f0f0f5 none !important;
		opacity: 1;
		pointer-events: none !important;
	}
}

.buttons-wrapper {
	display: flex;
	align-items: center;
	margin: -8px;

	.btn {
		margin: 8px;
	}
}
